.color {
  border-radius: 50%;
  display: inline-block;
  height: 1.5rem;
  margin-right: 0.5em;
  position: relative;
  width: 1.5rem;
  vertical-align: bottom;

  $c: #555;
  $cpp: #f34b7d;
  $cr: #000100;
  $css: #563d7c;
  $html: #e34c26;
  $java: #b07219;
  $js: #f1e05a;
  $py: #3572a5;
  $r: #198ce7;
  $rb: #701516;
  $rs: #dea584;
  $sql: #ccc;
  $ts: #2b7489;

  &.c {
    background-color: $c;
  }

  &.cpp {
    background-color: $cpp;
  }

  &.cr {
    background-color: $cr;
  }

  &.css {
    background-color: $css;
  }

  &.html {
    background-color: $html;
  }

  &.java {
    background-color: $java;
  }

  &.js {
    background-color: $js;
  }

  &.py {
    background-color: $py;
  }

  &.r {
    background-color: $r;
  }

  &.rb {
    background-color: $rb;
  }

  &.rs {
    background-color: $rs;
  }

  &.sql {
    background-color: $sql;
  }

  &.ts {
    background-color: $ts;
  }
}
