@import "base";

@import "align";
@import "anchor";
@import "container";
@import "figure";
@import "indented";
@import "nav";

@import "custom/embedded";
@import "custom/filter";
@import "custom/icons";
@import "custom/language-colors";
@import "custom/notes";

@import "section/self";

.color-list {
  @media only screen and (max-width: $mobile-width) {
    display: none;
  }

  margin-top: 5em;
  line-height: 2.2rem;

  .emojis {
    font-size: 1.5rem;
  }
}

.summary {
  width: 80%;
}

.side-link {
  color: $faded-link-color;
  font-size: 0.5em;
  margin-left: 1em;
}

.cc-main {
  overflow: hidden;
}

li {
  &[data-domain="computing"] {
    list-style: disc;
  }
  &[data-domain="musings"] {
    list-style: circle;
  }
  &[data-domain="chemistry"] {
    list-style: square;
  }
}

.flex-space-between {
  display: flex;
  justify-content: space-between;
}

.post-name-line {
  .post-draft {
    color: $faded-color;

    &::before {
      content: "[DRAFT] ";
    }
  }

  .date {
    flex-basis: 8em;
    text-align: right;
  }
}

.fly {
  object-fit: contain;
  position: fixed;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -999;
  opacity: 0.95;
  filter: hue-rotate(-30deg);
}

.overhang {
  > * {
    display: inline-block;
    max-width: 60%;
    position: relative;
    background: transparent;
    z-index: 3;
    margin-left: 0;
    padding-left: 0;
    margin-right: 0;
    padding-right: 0;
  }

  > *:after {
    background-color: rgba(230, 214, 189, 1);
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: 100%;
    height: 100%;
    z-index: -1;
    margin: auto -1em;
    padding: 0 1em;
  }

  > p {
    // color: black;
  }
}
